import { useState, useEffect, useCallback } from "react";
import { conversationAPI, mockMessages } from "../services/api";

/**
 *       
 *     
 */
export const useMessages = (conversationId) => {
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [hasMore, setHasMore] = useState(true);
  const [nextCursor, setNextCursor] = useState(null);

  //  
  const loadMessages = useCallback(
    async (reset = false) => {
      if (!conversationId || loading) {
        console.log("useMessages -   :", {
          conversationId,
          loading,
        });
        return;
      }

      console.log("useMessages -   :", {
        conversationId,
        reset,
        cursor: reset ? null : nextCursor,
        currentMessagesCount: messages.length,
      });

      setLoading(true);
      setError(null);

      try {
        const cursor = reset ? null : nextCursor;
        const response = await conversationAPI.getMessages(
          conversationId,
          cursor
        );

        console.log("useMessages - API :", {
          conversationId,
          messagesReceived: response.messages?.length || 0,
          hasMore: response.hasMore,
          nextCursor: response.nextCursor,
          reset,
        });

        if (reset) {
          setMessages(response.messages);
        } else {
          //     ()
          setMessages((prev) => [...response.messages, ...prev]);
        }

        setHasMore(response.hasMore);
        setNextCursor(response.nextCursor);
      } catch (err) {
        console.error("  :", err);
        console.error(" conversationId:", conversationId);
        setError("  .");

        // API  mock  fallback
        const conversationMessages = mockMessages[conversationId] || [];
        console.log("useMessages - Mock  :", {
          conversationId,
          mockMessagesCount: conversationMessages.length,
        });

        if (reset) {
          setMessages(conversationMessages);
          setHasMore(false);
          setNextCursor(null);
        }
      } finally {
        setLoading(false);
      }
    },
    [conversationId, loading, nextCursor, messages.length]
  );

  //    ()
  const addMessage = useCallback((message) => {
    const newMessage = {
      id: message.timestamp || new Date().toISOString(),
      role: message.role,
      content: message.content,
      tokenCount: message.tokenCount || 0,
      timestamp: message.timestamp || new Date().toISOString(),
    };

    setMessages((prev) => [...prev, newMessage]);
    return newMessage;
  }, []);

  //   (   )
  const updateMessage = useCallback((messageId, updates) => {
    setMessages((prev) =>
      prev.map((msg) => (msg.id === messageId ? { ...msg, ...updates } : msg))
    );
  }, []);

  //  
  const removeMessage = useCallback((messageId) => {
    setMessages((prev) => prev.filter((msg) => msg.id !== messageId));
  }, []);

  //    ( )
  const loadPreviousMessages = useCallback(() => {
    if (hasMore && !loading) {
      return loadMessages(false);
    }
    return Promise.resolve();
  }, [hasMore, loading, loadMessages]);

  //   (  )
  const clearMessages = useCallback(() => {
    setMessages([]);
    setHasMore(true);
    setNextCursor(null);
    setError(null);
  }, []);

  // conversationId   
  useEffect(() => {
    console.log(" [DEBUG] useMessages - conversationId  :", {
      conversationId,
      conversationIdType: typeof conversationId,
      isConversationIdNull: conversationId === null,
      isConversationIdUndefined: conversationId === undefined,
      previousConversationId: conversationId, //       
    });

    if (conversationId) {
      console.log(
        " [DEBUG] useMessages -     :",
        conversationId
      );
      clearMessages();
      loadMessages(true);
    } else {
      console.log(
        " [DEBUG] useMessages - conversationId null/undefined,  "
      );
      clearMessages();
    }
  }, [conversationId]);

  return {
    messages,
    loading,
    error,
    hasMore,
    addMessage,
    updateMessage,
    removeMessage,
    loadPreviousMessages,
    clearMessages,
    refresh: () => loadMessages(true),
  };
};
